import { useState, useEffect } from "react";
import axios from "axios";

function useKakaoSearch(section, query, sort, page, size) {
  // section : (vclip), (image)
  const BASE_URL = `https://dapi.kakao.com/v2/search/${section}`;
  const API_KEY = process.env.REACT_APP_KAKAO_KEY;

  const [data, setData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const [hasMorePage, setHasMorePage] = useState(false);

  //    
  // ,     
  useEffect(() => {
    if (query === "") return;
    setData([]);
  }, [query]);

  //     
  useEffect(() => {
    setData([]);
  }, [sort]);

  // ,  , (  )   api  
  // ,     
  useEffect(() => {
    if (query === "") return;

    setIsLoading(true);
    setError(false);
    let cancel;

    axios({
      method: "GET",
      url: BASE_URL,
      headers: {
        Authorization: `KakaoAK ${API_KEY}`,
      },
      params: {
        query: query,
        sort: sort,
        page: page,
        size: size,
      },
      cancelToken: new axios.CancelToken((c) => (cancel = c)),
    })
      .then((res) => {
        setData((prev) => [...prev, ...res.data.documents]);
        setHasMorePage(!res.data.meta.is_end);
        setIsLoading(false);
      })
      .catch((e) => {
        if (axios.isCancel(e)) return;
        setIsLoading(false);
        setError(true);
      });

    return () => cancel();
  }, [query, sort, page]);

  return { data, isLoading, error, hasMorePage };
}

export default useKakaoSearch;
